@import url(https://fonts.googleapis.com/css?family=Rubik+Mono+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pathway+Gothic+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pathway+Gothic+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    line-height: 1.42em;
    color:#A7A1AE;
    background-color:#1F2739;
  }
  
  h1 {
    font-size:3em; 
    font-weight: 300;
    line-height:1em;
    text-align: center;
    color: #4DC3FA;
  }
  
  h2 {
    font-size:1em; 
    font-weight: 300;
    text-align: center;
    display: block;
    line-height:1em;
    padding-bottom: 2em;
    color: #FB667A;
  }
  
  h2 a {
    font-weight: 700;
    text-transform: uppercase;
    color: #FB667A;
    text-decoration: none;
  }
  /* #first,#second,#third{
    color:white;
    font-size:150%;
    background:#216c2a;
    font-family: 'Pathway Gothic One', sans-serif;
  } */
  #first{
    color:gold;
  }
  #second{
    color:silver;
  }
  #third{
    color:#cd7f32
  }

  .avatar{
      height:70px;
      width:70px;
      margin-right:10px;
      border-radius: 50%;
  }
  
  .blue { 
    color: #185875; 
    font-family: 'Pathway Gothic One', sans-serif;
    font-size:200%;
    
  
  }
  .yellow { color: #FFF842; }
  
  .container th h1 {
    font-weight: bold;
    font-size: 1em;
    text-align: left;
    color: #185875;
  }
  
  .container td {
        font-weight: normal;
        font-size: 1em;
    box-shadow: 0 2px 2px -2px #0E1119;
  }
  
  .container {
        text-align: left;
        overflow: hidden;
        width: 80%;
        margin: 0 auto;
    display: table;
    padding: 0 0 8em 0;
  }
  
  .container td, .container th {
        padding-bottom: 2%;
        padding-top: 2%;
    padding-left:2%;  
    font-family: 'Pathway Gothic One', sans-serif;
    font-size:150%;
  }
  
  /* Background-color of the odd rows */
  .container tr:nth-child(odd) {
        background-color: #323C50;
  }
  
  /* Background-color of the even rows */
  .container tr:nth-child(even) {
        background-color: #2C3446;
  }
  
  .container th {
        background-color: #1F2739;
  }
  
  .container td:first-child { color: #FB667A; }
  
  .container tr:hover {
     background-color: #464A52;
  box-shadow: 0 6px 6px -6px #0E1119;
  }
  
  .container td:hover {
    background-color: white;
    color: #403E10;
    font-weight: bold;
    
    /*  */
    -webkit-transform: translate3d(6px, -6px, 0);
            transform: translate3d(6px, -6px, 0);
    
    -webkit-transition-delay: 0s;
    
            transition-delay: 0s;
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s;
    -webkit-transition-property: all;
    transition-property: all;
    -webkit-transition-timing-function: line;
            transition-timing-function: line;
  }
  
  @media (max-width: 800px) {
  .container td:nth-child(4),
  .container th:nth-child(4) { display: none; }
  }

  @media only screen and (min-device-width: 414px) and (max-device-height: 736px) and (-webkit-device-pixel-ratio: 3) { 

    .blue{
        font-size:20px;
    }
    table, thead, tbody, th, td, tr { 
		display: block; 
    }
    
    thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	td:nth-of-type(1):before { content: "Rank: "; }
	td:nth-of-type(3):before { content: "Wins: "; }
	td:nth-of-type(5):before { content: "Loses: "; }
	td:nth-of-type(6):before { content: "Total bets: "; }

  }

